.container {
  display: flex;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 1px;
  
    
  align-items: center;
  
  width: 36rem;
  border-radius: 1rem;
  background-color: white;
  box-shadow: rgb(0 0 0 / 40%) 0px 2px 4px, rgb(0 0 0 / 30%) 0px 7px 13px -3px, rgb(0 0 0 / 20%) 0px -3px 0px inset;
}

.picture {
  width: 14rem;
  height: 14rem;
  border-radius: 200px;
  
}

.container_left {
  padding: 20px;
  margin: 10px;
}

