.container_left_title {
  color: #ff914d;
  font-size: x-large;
  font-weight: bolder;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.container_left_location {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
  font-style: italic;
  font-size: large;
  font-weight: bold;
  line-height: normal;
}

.container_left_content {
 
  border-radius: 20px;
  letter-spacing: 1px;
  line-height: normal; 
  
  text-justify: distribute;
}

.container_right {
  padding: 20px;
  margin: 10px;
}

.api {
  padding: 20px;
  
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.api_btn {
  color: white;
  white-space: nowrap;
  background-color: #B5876D;
  width: 5rem;
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin-right: 1rem;
  font-size: 1rem;
  box-shadow: rgb(0 0 0 / 40%) 0px 2px 2px, rgb(0 0 0 / 30%) 0px 6px 8px -3px, rgb(0 0 0 / 20%) 0px -3px 0px inset;
  font-weight: bolder;
}

