.black_nav {
  margin-top: 100px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.hotplace {
  color: white;
  white-space: nowrap;
  background-color: #ff914d;
  width: 5rem;
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin-right: 1rem;
  font-size: 1rem;
  box-shadow: rgb(0 0 0 / 40%) 0px 2px 2px, rgb(0 0 0 / 30%) 0px 6px 8px -3px,
    rgb(0 0 0 / 20%) 0px -3px 0px inset;
  cursor: pointer;
  font-weight: bolder;
}



::-webkit-scrollbar {

  display: none;
}

